

.blogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.blog-page {
    width: 100%;
    height: fit-content;
    min-height: 100%;
    margin-top: $header-height;
    background-color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.05rem;
    line-height: 1.5;
    font-weight: 400;
    --source-text-decoration-thickness: 2px;
    font-weight: 300;
    word-wrap: break-word;
    padding-top: 20px;
    padding-bottom: 60px;
}

.blog-page > nav {
    padding-top: 10px;
    padding-left: 15px;
}

.blog-page {
    a:link {
        color: #666;
    }

    /* visited link */
    a:visited {
        color: #666;
    }

    /* mouse over link */
    a:hover {
        color: #666;
    }

    /* selected link */
    a:active {
        color: #666;
    }
}

.blog-page b {
    color: #666;
}

.blog-page h1 {
    font-weight: 700;
}

.blog-page main {
    max-width: 800px;
    margin: auto;
    padding: 10px;
}

.blog-page sup {
    font-size: small;
}

.blog-page h1 {
    font-weight: 700;
}

.blog-summary {
    padding: 0 2%;
    line-height: 1.1;
    max-width: 450px;
    width: 100%;
    font-size: 0.95rem;
}

div.blog-summary img {
    max-width: 450px;
    width: 100%;
    height: auto;
}

.blog-summary h3 {
    height: 3em;
}

.blog-content-column {
    padding: 10%;
    width: 80%;
}

.blog-summary-column {
    width: 20%;
}

.blog-summary-header {
    position: relative;
}

