.blog-page img {
    margin: 30px 0;
    width: 100%;
    opacity: 0.8;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.blog-page b {
    color: #444;
}

.blog-page iframe {
    margin: 30px 0;
    max-width: 560px;
    width: 100%;
}
