/* Customize the label (the container) */
.checkboxLabel {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 4px;
  margin-top: 4px;
  cursor: pointer;
  @include no-user-select();
}

/* Hide the browser's default checkbox */
.checkboxLabel input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Checkbox */
.checkboxCheckmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #ccc;
  border: 1px solid #666;
}

.checkboxLabel input:checked~.checkboxCheckmark {
  background-color: #888;
}

.checkboxCheckmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkboxLabel input:checked~.checkboxCheckmark:after {
  display: block;
}

.checkboxLabel .checkboxCheckmark:after {
  left: 6px;
  top: 2 px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.radioContainer {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 2px;
  cursor: pointer;
  @include no-user-select();
}

.radioContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.radioCheckmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #ccc;
  border: 2px solid #888;
  border-radius: 50%;
}

.radioContainer input:checked~.radioCheckmark {
  background-color: $primarycol;
}

.radioCheckmark:after {
  content: "";
  position: absolute;
  display: none;
}

.radioContainer input:checked~.radioCheckmark:after {
  display: block;
}

.container .radioCheckmark:after {
  top: 9px;
  left: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

