.anim-control-container {
  position: absolute;
  left: 50%;
  display: flex;
  /* align-items: center; */
  flex-direction: row;
  transform: translate(-50%);
  background-color: rgba(128, 128, 128, 0.5);
  padding-left: 10px;
  padding-right: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 8px;
  justify-content: space-between;
  width: 160px;
}

@media only screen and (max-width: 375px) {
  .anim-control-container {
    bottom: 34px;
  }
}

@media only screen and (min-width: 375px) {
  .anim-control-container {
    bottom: 5px;
  }
}


.anim-control-container select {
  width: 110px;
  text-overflow: ellipsis;
}

.anim-control-button {
  width: 30px;
  height: 30px;
  opacity: 0.5;
}

#modelviewer-container {
  margin-top: $header-height;
  //  height: $viewport-height;
  position: relative;
}

.model-info-window {
  position: absolute;
  max-width: 500px;
  width: 90%;
  background-color: rgba(128, 128, 128, 0.5);
  color: $textcol-light;
  border: 1px solid #888;
  border-radius: 10px;
  z-index: 2000;
  padding: 15px;
  padding-top: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.model-info-header {
  font-size: larger;
  display: flex;
  border-bottom: 1px solid #444;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.model-info-body {
  text-align: left;
  overflow-x: scroll;
  overflow-y: scroll;
  scrollbar-width: none;
  /* Firefox */
  -ms-overflow-style: none;
  white-space: pre-line;
  /* Internet Explorer 10+ */
}

.model-info-social svg {
  margin: 0 3px;
}

.model-info-social {
  display: flex;
  margin: 10px 0;
  justify-content: center;
}

.model-info-body::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}

.model-selector-root {
  @include transition;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  overflow-x: hidden;
}

.model-selector-container-outer {
  width: 100%;
  padding-top: 30px;
}

.model-selector-container-inner {
  display: flex;
  flex-wrap: nowrap;
  overflow-y: hidden;
  overflow-x: scroll;
}

.model-selector-container-inner-noscroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.model-selector-container-inner-noscroll {
  display: flex;
  flex-wrap: nowrap;
  overflow-y: hidden;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.model-selector-item {
  margin: 10px 10px;
  display: flex;
  flex-direction: column;
}

.model-selector-item-selected {
  @extend .model-selector-item;
  border: 1px solid white;
}

.model-selector-item-title {
  width: 128px;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
  text-align: center;
  white-space: nowrap;
  margin-bottom: 10px;
  @include no-user-select();
}

.model-selector-item-image {
  min-width: 128px;
  min-height: 96px;
  width: 128px;
  height: 96px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.model-selector-close-button {
  position: absolute;
  right: 0;
  top: 0;
}

.debug-window {
  position: absolute;
  width: 250px;
  left: 10px;
  top: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 20px;
}

.debug-window-close {
  position: absolute;
  right: 5px;
  top: 5px;
}

.iphone-x-video {
  position: absolute;
  width: 100%;
  left: 0;
  display: block;
}

.iphone-x-video-large {
  position: absolute;
