
.docpage {
  display: flex;
  align-items: stretch;
  width: 100%;
  height: fit-content;
  min-height: 100%;
  overflow: visible;
  padding: 5%;
  padding-top: 25px;
  padding-bottom: 25px;
  margin-top: $header-height;
  max-width: 1280px;
  background-color: white;
  font-size: 1.1rem;
}

.docpage table {
  color: black;
  box-shadow: none;
  border: none;
}

.docpage tr {
  background-color: white;
  border: none;
  
}

.docpage h1,h2,h3,h4,h5 {
    margin-block-start: 0.8em;
    margin-block-end: 0.8em;
}

.docpage p {
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
}

b {
    color: #666;
}

.docpage-index {
    /* justify-items: start;
    flex-direction: column;
    font-size: large;
    display: flex;
    align-items: stretch;
    padding-right: 20px;
    line-height: 1.8rem; */
    display: flex;
    flex-direction: column;

    -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding: 20px;
  line-height: 1.8rem;
}

.docpage-content {
 -webkit-flex: 4;
  -ms-flex: 4;
  flex: 4;
  padding: 25px;
  min-width: 0;
}


@media only screen and (max-width: 600px) {
  .docpage {
    flex-direction: column;
  }
}

.docpage-index a { color: black }
.docpage-index a:hover { color:black; }