$footercol: #555;

.footer {
    @include no-user-select();
    background-color: $footercol;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: large;
    padding: 20px 10px;
    min-height: 60px;
    width: 100%;
    position: relative;
  }
  
  .footer img {
    margin: 0 8px;
    width: 24px;
  }
  
  .footer-social-links {
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .footer-social-links span {
    color: $primarycol;
  }
  
  .footer-links {
    display: flex;
    justify-content: space-evenly;
    width: 90%;
  }
  
  .footer-links a {
    display: inline-block;
    margin-bottom: 2px 0;
  }
  
  .footer-links > div {
    border-left: 1px solid #aaa;
    padding: 0 10px;
  }
  