$headercol: #555;
$headercol-light: #aaa;

.header-container {
    @include medium-shadow();
    @include no-user-select();
    position: fixed;
    background-color: $headercol;
    width: 100%;
    height: $header-height;
    padding: 10px;
    display: flex;
    align-items: center;
    z-index: 1000;
    top: 0;
    text-transform: uppercase;
    color: $primarycol;
  }
  
  .header-logo {
    max-width: 130px;
    margin-right: 10px;
  }
  
  .header-logo img {
    padding-left: 5px;
    max-width: 130px;
  }
  
  .header-login-button {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  
  .header-login-button > a {
    margin-right: 10px; 
  }
  
  