.page {
    @extend .fullwidth-page;
    background-color: white;
  }

  p {
    width: 100%;

  }
  
  .page-section-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .page-section {
    @extend .fullwidth-section;
    align-content: center;
    max-width: 1200px;
    width: 100%;
    flex-wrap: wrap;
    padding: 10px;
  }
  
  .page-section .flexrow {
    align-items: stretch !important;
  }
  
  .page-section .flexrow {
    align-items: stretch !important;
  }
  
  .page-section .flexcol {
    justify-content: center;
  }
  
  .page-section h1 {
    text-align: center;
  }
  
  .page-section q {
    font-weight: 400;
  }
  
  .page-section-img {
    margin: 20px auto;
    display: block;
    max-width: 500px;
    width: 100%;
    border-radius: 10px;
    opacity: 0.9;
  }
  
  .page-section iframe {
    display: block;
    margin: 0 auto;
    border: none;
    width: 100%;
    height: 100%;
  }
  
  .page-video {
    width: 100%;
  }
  
  .page-viewer {
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
  }
  
  .page .flexcol:first-child {
    padding-right: 5%;
    flex: 1;
  }
  
  .page .flexcol:last-child {
    flex: 2;
  }
  
  .page-images-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    max-width: 800px;
    margin: auto;
  }
  
  .page-detail-container {
    display: flex;
    flex-wrap: wrap;
    margin: 20px auto;
    justify-content: center;
    text-align: left !important;
    max-width: 800px;
    align-items: center;
  }
  
  .page-detail-container>div {
    text-align: left !important;
    padding: 15px;
    flex: 1;
    min-width: 300px;
  }
  
  .page-detail-container img {
    max-width: 400px;
    width: 100%;
  }
  
  .page-detail-container q {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500 !important;
  }
  
  .page-button-base {
    background-color: $primarycol;
    border: none;
    color: white !important;
    font-weight: bold;
    padding: 8px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 20px;
    margin: 20px 0;
  }
  
  .page-button {
    @extend .page-button-base;
  }
  
  .page-button-medium {
    @extend .page-button-base;
  }
  
  .page-button-large {
    @extend .page-button-base;
  }
  
  .page-button-large a {
    color: white;
  }
  
  .page-button-medium a {
    color: white;
  }
  
  .home-page-feature-img {
    @include light-shadow();
    max-width: 1200px;
    width: 100%;
    margin: 20px auto;
    border-radius: 20px;
  }
  
  .page-feature-icon {
    width: 80px !important;
    margin-bottom: 15px;
  }
  
  .page-configurator-aspect {
    @include aspect-ratio(3, 2);
  }
  
  @media only screen and (max-width: 600px) {
    .page-configurator-aspect {
      @include aspect-ratio(2, 3);
    }
  }
  