$appbar-height: 30px;

.editor-main {
  /*   position:  absolute;
    overflow-x: hidden;
    
    margin-top: $header-height;
    font-family: 'Roboto', sans-serif; */
  margin-top: $header-height + $appbar-height;
  position: relative;
  height: calc(100vh - #{$header-height + $appbar-height});
}

.editor-app-bar {
  width: 100%;
  position: fixed;
  background-color: #888;
  height: $appbar-height;
  display: flex;
  align-items: center;
  top: $header-height;
  z-index: 1000;
  padding-left: 8px;
}

#editor-ezv-canvas-container {
  overflow: hidden;
  width: 100%; 
  display: block;
  position: relative;
  flex: 1;
}

.editor-sidebar {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-left: 5px;
  padding-bottom: 30px;
  font-size: 0.9rem;
  background-color: #424242;
  color: #a8a8a8;
  @include no-user-select();
}
/* 
.editor-sidebar-container {
  width: 100%;
  min-height: 100%;
  height: fit-content;
  padding-left: 10px;
} */

.editor-sidebar-heading {
  @include no-user-select;
  background-color: #343434;
  color: #a8a8a8;
  padding: 4px;
  margin-top: 8px;
  margin-bottom: 8px;
  margin-left: -5px;
  padding-left: 10px;
  width: 105%;
}

.editor-sidebar-subheading {
  @extend .editor-sidebar-heading;
  @include no-user-select;
  margin-left: 0px;
  background-color: #666;
  color: #ccc;
}

.editor-sidebar input {
  background-color: #585858;
  border: none;
  color: #aaa;
}

.editor-app-menu {
  color: white;
  margin: 0 10px;
  cursor: pointer;
  display: flex;
  height: 100%;
  align-items: center;
}

.editor-product-name {
  @include no-user-select;
  color: white;
  flex: 1 1;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.ctl-label {
  @include no-user-select;
  margin-top: 4px;
}

.command-bar-container {
  display: flex;
  position: relative;
  align-items: center;
  height: 32px;
  margin-right: 10px;
}

.command-bar-item {
  padding-bottom: 2px;
  padding-top: 2px;
}

.image-thumb {
  border: 1px solid #444;
  margin: 5px 0;
  max-width: 300px;
}

.editor-container {
  width: 100%;
  height: calc(100% - 90px);
  margin-top: 90px;
  position: relative;
}

.editor-main-container {
  width: calc(100% - 360px);
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.editor-sidebar-container {
  background-color: $panel-background;
  width: 360px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
}

.editor-variant-colors-container {
  display: flex;
  flex-direction: column;
}

.editor-variant-colors-row {
  display: flex;
  flex-direction: row;
}
