.help-page {
    @extend .fullwidth-page;
    background-color: white;
    padding: 2% 10%;
}


@media only screen and (max-width: 600px) {
  .help-page {
    padding: 2% 3%;     
  }
}

.help-page img {
  border-radius: 5px;
  margin-top: 35px;
  margin-bottom: 35px;
  flex-direction: row;
  align-items: center;
  margin-right: 15px;
  vertical-align: middle;
}

@media only screen and (max-width: 600px) {
.help-page img {
    max-width: 100%;
  }
}

@media only screen and (min-width: 600px) {
.help-page img {
    max-width: 800px;
  }
}

.help-page nav {
  margin-bottom: 12px;
}

.help-page-video-link {
  @include light-shadow();
  width: 280px;
}

.help-page-links-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.help-page-links-row span {
  background-color: #888;
  float: left;
  border: none;
  outline: none;
  padding: 8px 8px;
  @include transition();
  margin-right: 10px;
  margin-bottom: 2px;
  color: black; 
}

.help-index {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 25px 0;
}

.help-index > div {
  background-color: white;
  padding: 35px;
  margin: 15px;
  border: 1px solid #ccc;
  border-radius: 10px;
  @include light-shadow();
}
