.full-width-header {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding-bottom: 43%;
  display: flex;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
  height: 700px;
}

.full-width-header img {
  opacity: 0.9;
}

.full-width-header-text {
  color: black;
  max-width: 800px;
  text-align: center;
}

.full-width-header-promo-button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%, 0);
}

.feature-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-auto-rows: minmax(min-content, max-content);
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  font-size: 0.9rem;
  margin-top: 20px;
  max-width: 1024px;
  width: 100%;
}

.feature-container h4 {
  font-weight: 700;
  margin-top: 25px !important;
  margin-bottom: 20px !important;
}

.feature-container>div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  background-color: #f8f8f8;
  border-radius: 10px;
}

.feature-container img {
  height: 60px;
  margin-left: auto;
  opacity: 0.8;
}

.feature-container img.medium {
  height: 180px;
  margin-left: auto;
  opacity: 0.8;
}

.product-features-row {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  justify-content: space-evenly;
  min-height: 350px;  
}

.product-features-box {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  justify-content: space-evenly;
  font-family: Montserrat;
}

.product-features-box div:first-child {
  flex: 1;
  padding: 0 2%;
  min-width: 280px;
}

.product-features-box div:last-child {
  flex: 1;
  padding: 0 2%;
}

.product-features-box img {
  width: 300px;
  opacity: 0.95;
}

.product-features-box-builder-image {
  width: 150px;
}