.progress-container {
    width: 200px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font: 18px Arial, sans-serif;
  }
  
  .progress-bar {
      width: 0%;
      height: 100%;
      background-color: #fff;
      -webkit-transition: width .6s ease;
      -o-transition: width .6s ease;
      transition: width .6s ease;
      overflow: visible;
  }
  
  .progress {
      height: 6px;
      margin-bottom: 20px;
      overflow: hidden;
      border: 2px solid #444;
      border-radius: 4px;
      background-color: #333;
  }
  
  .progress-indicator {
      position: relative;
      width: 12px;
      height: 6px;
      border-radius: 50%;
      background-color: #222;
      top: -3px;
  }
  
  .progress-text {
    color: white;
     text-shadow:
      -1px -1px 0 #222,
      1px -1px 0 #222,
      -1px 1px 0 #222,
      1px 1px 0 #222;
  }
  